@import "../../utils/commons.scss";

.container {
  @extend .flexRow;
  justify-content: space-between;
  min-height: 100px;
  padding: 0 1rem;
  box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.1);

  .navItems {
    @extend .flexRow;
    justify-content: space-between;

    list-style: none;
    text-decoration: none;
    a {
      text-decoration: none;
      color: var(--clr-text-dark);
    }
  }
  .leftNav {
    width: 12%;
    margin-left: calc((100% - var(--fixed-width)) / 2);
  }
  .rightNav {
    width: 20%;
    margin-right: calc((100% - var(--fixed-width)) / 2);
  }

  .burgerMenu {
    width: 1.6rem;
    display: none;
    .burger {
      width: 100%;
      height: 3px;
      background: black;
      margin: 4px 0;
    }
  }

  @media screen and (max-width: $media-lg) {
    .leftNav {
      width: 15%;
    }
    .rightNav {
      width: 25%;
    }
  }

  @media screen and (max-width: $media-md) {
    .leftNav {
      width: 25%;
    }
    .rightNav {
      width: 35%;
    }
  }

  @media screen and (max-width: $media-sm) {
    .leftNav {
      display: none;
    }
    .rightNav {
      display: none;
    }
    .burgerMenu {
      display: block;
    }
  }
}
